async function doesUserExist(username){
  const q = query(collection(db, "users"), where("username", "==", username));

  const querySnapshot = await getDocs(q);
  return !querySnapshot.empty

}

function getUser(uid){
  return query(collection(db, "users"), where("userId", "==", uid));

}
async function addUser(user,username,fullName,emailAddress){
  return await addDoc(collection(db, "users"), {
    fullName,
    emailAddress,
    userId: user.uid,
    username: username.toLowerCase(),
    followers: [],
    following: [],
    dateCreated: Date.now()
  });
}
async function addTweet(user,tweet){
  return await addDoc(collection(db,'timeline'),{
    user,
    tweet,
    replies:[],
    likes:[],
    timestamp: serverTimestamp()
  })
}

async function getSuggestedAccounts(id){
  const q = query(collection(db, "users"), where("userId", "!=", id),limit(4));
  const data = [];
  const querySnapshot = await getDocs(q);
  querySnapshot.forEach((doc) => {
    data.push({id:doc.id,...doc.data()});
  });
  
  return data.filter(acc=> !acc.followers.includes(id));
}
async function getRooms(id){
  const q = query(collection(db, "rooms"), where("members", "array-contains", id),orderBy('lastMsg.timestamp'),limit(12));
  const data = [];
  const querySnapshot = await getDocs(q);
  querySnapshot.forEach((doc) => {
    data.push({id:doc.id,...doc.data()});
  });
  return data
}



async function updateFollowers(acc,user){
  await updateDoc(doc(db,'users',acc.id), {
    followers: arrayUnion(user.userId)
  });
  await updateDoc(doc(db,'users',user.id), {
    following: arrayUnion(acc.userId)
  });
}
async function removeFollowers(acc,user){
  await updateDoc(doc(db,'users',acc.id), {
    followers: arrayRemove(user.userId)
  });
  await updateDoc(doc(db,'users',user.id), {
    following: arrayRemove(acc.userId)
  });
}
async function updateLikes(tweet,user){
  await updateDoc(doc(db,'timeline',tweet), {
    likes: arrayUnion(user)
  });
}
async function getTweet(tweet){
  const docRef = doc(db, "timeline", tweet);
  const docSnap = await getDoc(docRef);
  return {id: tweet, ...docSnap.data()};
}

function getMessages(roomId){
  return query(collection(db, 'rooms', roomId, 'messages'), orderBy('timestamp', 'desc'), limit(20))
}
async function addReply(id,reply){
  await updateDoc(doc(db,'timeline',id), {
    replies: arrayUnion(reply)
  });
}
async function addMessage(userId,roomId,text) {
  // Add a new message entry to the Firebase database.
  try {
    const lastMsg = await addDoc(collection(db, 'rooms', roomId, 'messages'), {
      userId,
      text,
      timestamp: serverTimestamp()
    });
    const docSnap = await getDoc(doc(db, "rooms", roomId, 'messages', lastMsg.id))
    await updateDoc(doc(db,'rooms',roomId), {
      lastMsg: docSnap.data() 
    });
  }
  catch(error) {
    console.error('Error writing new message to Firebase Database', error);
  }
}

