
import { HashRouter,Routes, Route } from 'react-router-dom';
import React, { lazy, Suspense } from "react";




const Login = lazy(() => import('../pages/Login'));
const  Signup = lazy(() => import('../pages/Signup'));
const  NotFound = lazy(() => import('../pages/NotFound'));
const App = lazy(() => import('../App'));
const Tweetpg = lazy(() => import('../pages/Tweetpg'));
const Timeline = lazy(() => import('./Timeline'));
const Messages = lazy(() => import('../pages/Messages'));
const Profile = lazy(() => import('./Profile'));
const ProfileTweets = lazy(() => import('./ProfileTweets'));
const ProfileLikes = lazy(() => import('./ProfileLikes'));


function RouteSwitch() {
  return (
    <HashRouter>
      <Suspense fallback={<div className='h-screen w-screen text-4xl flex items-center justify-center'>
          <div className=""><span class="loader"></span></div>
        </div>}>
        <Routes>
          <Route path='/' element={<App/>}>
            <Route index element={<Timeline/>}/>
            <Route path='messages/*' element={<Messages/>}/>
            <Route path='p/:userId/*' element={<Profile/>}>
              <Route index element={<ProfileTweets/>}/>
              <Route path='likes' element={<ProfileLikes/>}/>
            </Route>
            <Route path=':tweetID' element={<Tweetpg/>}/>
          </Route>
          <Route path='login' element={<Login/>}/>
          <Route path='signup' element={<Signup/>}/>
          <Route path="*" element={<NotFound />} />
        </Routes>
      </Suspense>
    </HashRouter>
  )
}

export default RouteSwitch;